import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginPage } from '../pages/login/login';
import { AreasPage } from '../pages/areas/areas';
import { HabilidadesPage } from '../pages/habilidades/habilidades';
import { NuevaPublicacionPage } from '../pages/nueva-publicacion/nueva-publicacion';
import { PerfilPage } from '../pages/perfil/perfil';
import { EstudiantePage } from '../pages/estudiante/estudiante';
import { ProfesorPage } from '../pages/profesor/profesor';
import { PublicacionesPage } from '../pages/publicaciones/publicaciones';
import { InicioPage } from '../pages/inicio/inicio';

import { HttpClientModule } from "@angular/common/http";

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LoginPage,
    AreasPage,
    HabilidadesPage,
    NuevaPublicacionPage,
    PerfilPage,
    EstudiantePage,
    ProfesorPage,
    PublicacionesPage,
    InicioPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LoginPage,
    AreasPage,
    HabilidadesPage,
    NuevaPublicacionPage,
    PerfilPage,
    EstudiantePage,
    ProfesorPage,
    PublicacionesPage,
    InicioPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



